.table {
  border-collapse: collapse;
  margin: 25px 0;
  font-size: 0.9em;
  font-family: sans-serif;
  width: 100%;
}
.head {
  background-color: transparent;
  height: auto;
}
.row {
  color: #ffffff;
  width: auto;
  padding: 16px 14px;
  border-bottom: 1px solid #ffffff33;
  display: flex;
  justify-content: space-between;
}
.headRow {
  display: flex;
  justify-content: space-between;
  color: #ffffff;
  width: auto;
  padding: 16px;
  background: rgba(82, 59, 126, 0.6);
  border-radius: 8px;
  box-shadow: 0px 4px 60px 0px rgba(0, 0, 0, 0.25);
}
.cell {
  width: auto;
  display: flex;
  align-items: center;
  gap: 16px;
}
